.imageSliderContainer {
    width: 100%;
    height: 750px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    border-radius: 20px;
}

.slick-dots li {
    background: black;
    width: 20px;
}
.slick-dots li button {
    width: 10px;
    height: 10px;
    background: green;
    /* border-radius: 100%; */
}
.ant-carousel .slick-dots li.slick-active button {
    width: 25px;
    height: 5px;

    background: coral;
}

.imageContainer {
    width: 750px;
    height: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageContainer:hover {
    cursor: pointer;
}

.hoverImage {
    position: relative;
    width: 100%;
    height: auto;
}

.hoverImage:hover {
    transition: 0.5s;
}

.imageSlider {
    position: relative;
    object-fit: contain;
    width: 100%;
    height: 600px;
    padding-top: 20px;
}

.imageSlider:hover {
    transition: 0.3s;
    opacity: 0.7;
}

.imageDesc {
    animation-name: move;
    animation-direction: alternate;
    animation-duration: 0.3s;
    z-index: 1;
    font-weight: 100;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
        "Lucida Sans", Arial, sans-serif;
    font-size: 1.3rem;
    transition: all 0.8s;
    text-align: center;
    background-color: black;
    color: coral;
    position: absolute;
    width: 100%;
    height: auto;
    min-height: 130px;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
}

@keyframes move {
    from {
        bottom: -100px;
        opacity: 0;
    }
    to {
        bottom: 0;
        opacity: 0.8;
    }
}
